import React, { Component } from 'react'
import '../HomeContent/HomeContent.css'
// import imageIcon
import ImageHeader from '../../ImageIcon/iconFront.png'
import ImageHeader2 from '../../ImageIcon/iconFront2.png'
import ImageHeader3 from '../../ImageIcon/iconFront3.png'
// import ImagecHARCTER
// import IconChart from '../../ImageIcon/CharIcon/iconFront.png'
// import IconChart2 from '../../ImageIcon/CharIcon/iconFront2.png'
// import IconChart3 from '../../ImageIcon/CharIcon/iconFront3.png'
// import Programming Languacge Icon
import jsIcon from '../../ImageIcon/CharIcon/jsIcon.png'
import reactIcon from '../../ImageIcon/CharIcon/reactIcon.png'
import reactIcon2 from '../../ImageIcon/CharIcon/reactIcon2.png'
import ToTopIcon from '../../ImageIcon/CharIcon/toTop.png'


class HomeContent extends Component {
// scroll to top
    constructor(props) {
        super(props);
        this.state = {
          is_visible: false
        };
      }
    componentDidMount() {
        var scrollComponent = this;
        document.addEventListener("scroll", function(e) {
          scrollComponent.toggleVisibility();
        });
    }
  
    toggleVisibility() {
        if (window.pageYOffset > 850) {
            this.setState({
              is_visible: true
            });
          } else {
            this.setState({
              is_visible: false
            });
          }
    }
    scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
          });
    }
    render() {
        return (
            <div className="container-fluid bg-dark text-light">
                <div className="jumbotron bg-dark">
                    <div className="row">
                    {/* content left */}
                    <div className="col-md-6">
                        <h1 className="">Selamat datang</h1>
                        <p className="txt-right">Belajar Bersama kami,</p>
                        <p className="txt-right2  ">Hi sobat coder, Untuk kalian yang tertarik didunia programming terutama javascript, kalian bisa ikut bergabung dan belajar bersama dengan teman-teman lainya yang juga tertarik dengan Javascript, disini kita belajar dari Basic kemudian masuk pembelajaran ke Library pada Javascript, See you</p>
                        <button className="btn btn-info">Selengkapnya</button>
                    </div>

                    {/* content right */}
                    <div className="col-md-6    ">
                    <div id="carouselExampleFade" className="carousel slide d-block" data-ride="carousel">
                        <div className="carousel-inner">
                            <div className="carousel-item active" data-interval="2000">
                            <img src={ImageHeader} className="Img-left d-block w-100" alt="..." />
                            </div>
                            <div className="carousel-item"  data-interval="3000">
                            <img src={ImageHeader2} className="Img-left d-block w-100" alt="..." />
                            </div>
                            <div className="carousel-item" data-interval="4000">
                            <img src={ImageHeader3} className="Img-left d-block w-100" alt="..." />
                            </div>
                        </div>
                            <a className="carousel-control-prev" href="#carouselExampleFade" role="button" data-slide="prev">
                                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span className="sr-only">Previous</span>
                            </a>
                            <a className="carousel-control-next" href="#carouselExampleFade" role="button" data-slide="next">
                                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                <span className="sr-only">Next</span>
                            </a>
                        </div>
                    </div>
                    {/* content right */}
                    
                    {/* content card */}
                    <div className="row">
                    <div className="container-fluid rounded">
                        <h1 className="title-blog text-light text-center mt-5 mb-5">APA YANG AKAN KITA PELAJARI ?</h1><hr/>
                        <div className="row">
                        <div className="col-md-4 text-dark">
                            <div className="card-deck">
                                <div className="card">
                                    <img src={jsIcon} className="card-img-top" alt=""/>
                                    <h5 className="card-title">Basic Javascript</h5>
                                    <p className="description card-title">bahasa pemrograman tingkat tinggi dan dinamis. JavaScript populer di internet dan dapat bekerja di sebagian besar penjelajah web populer seperti Google Chrome, Internet Explorer, Mozilla Firefox, Netscape dan Opera</p>
                                    <button className="btn btn-primary">selengkapnya</button>
                                </div>
                            </div>
                        </div>
                        
                        <div className="col-md-4 text-dark">
                        <div className="card-deck">
                                <div className="card">
                                    <img src={reactIcon} className="card-img-top" alt=""/>
                                    <h5 className="card-title">ReactJs</h5>
                                    <p className="description card-title">React is a JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies. React can be used as a base in the development of single-page or mobile applications.</p>
                                    <button className="btn btn-primary">selengkapnya</button>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4 text-dark">
                        <div className="card-deck">
                                <div className="card">
                                    <img src={reactIcon2} className="card-img-top" alt=""/>
                                    <h5 className="card-title">ReactJs</h5>
                                    <p className="description card-title">React is a JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies. React can be used as a base in the development of single-page or mobile applications.</p>
                                    <button className="btn btn-primary">selengkapnya</button>
                                </div>
                            </div>
                        </div>


                        </div>
                    </div>
                </div>
                    {/* content card */}
                    
                    <div className="container bg-dark mt-5">
                        <div className="row text-center">
                            <div className="col text-center">
                                <h4>Tertarik untuk bergabung ?</h4>
                                <button type="button" className="btn btn-success" data-toggle="modal" data-target="#exampleModal" data-whatever="@mdo">Mendaftar</button>
                            </div>
                        </div>
                        
                        {/* form registrasi */}
                        <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title text-dark" id="exampleModalLabel">Silahkan Mendaftar</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body text-dark">
                                    <form>
                                        <div className="form-group">
                                            <label htmlFor="exampleInputEmail1">Nama antum</label>
                                            <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Masukan nama antum" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="exampleInputEmail1">Email antum</label>
                                            <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Masukan email antum" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="exampleInputPassword1">Password</label>
                                            <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
                                        </div>
                                        <div className="form-group form-check">
                                            <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                                            <label className="form-check-label" htmlFor="exampleCheck1">Saya setuju</label>
                                        </div>
                                    <button type="submit" className="btn btn-primary">Submit</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* form registrasi */}
                </div>
            {/* end row */}
                        {this.state.is_visible && <button onClick={this.scrollToTop} id="myBtn" title="Go to top">
                            <img src={ToTopIcon} alt=""/>
                        </button>}
                    </div>
                </div>
            </div>
        )
    }
}
// end scroll to top
export default HomeContent
