import React, { Component } from 'react'
import '../Routing/Routing.css'
import {Route,Link, BrowserRouter} from "react-router-dom";
import Drawer from 'react-motion-drawer';
import IM from '../../ImageIcon/CharIcon/IM.jpeg'
// contact
import Contacts from '../../Container/Contact/Contacts'
import Profile from '../../Container/Profile/Profile'
import HomeContent from '../../Container/HomeContent/HomeContent'
import gmb from '../../ImageIcon/CharIcon/github.png'
import navburger from '../../ImageIcon/CharIcon/nav.png'
import Melogo from '../../ImageIcon/CharIcon/LogoMe.png'

class Routing extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             isDrawerOpen: false
        }
    }
    
    toggleDrawer = () => {
        this.setState(prevState => ({
            isDrawerOpen: !prevState.isDrawerOpen
        }))
    }

    render() {
        return (
        <BrowserRouter>
            <nav className="navbar fixed-top navbar-expand-sm">
                <img src={Melogo} alt=""/><h5>Iko Mahendra</h5>
                <button id="tombolanav" className="navbar-toggler ml-auto"  type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <img src={navburger} alt="" onClick={this.toggleDrawer}/>
                </button>
            <Drawer className="wrap" open={this.state.isDrawerOpen}>
                <div className="row text-center">
                    <div className="col-sm-12">
                    <img className="rounded-pill mt-5" src={IM} alt=""/>
                    <h5 className="text-center mt-3 text-light">Diko Mahendra</h5>
                    <p className="text-center text-light mb-3">Frontend Developer</p>
                    <hr/>
                    </div>
                </div>
                <div className="row text-center">
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav">
                                <li className="nav-item active" >
                                    <Link to="/" className="nav-link text-light">Home</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/profile" className="nav-link text-white">Profile</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/contact" className="nav-link text-light">Contact</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/projects" className="nav-link text-light">Projects</Link>
                                </li>
                            </ul>
                        </div>
                </div>
                </Drawer>
            </nav>
            <div className="">
                <Route path="/" exact component={HomeContent} />
                <Route path="/profile" component={Profile} />
                <Route path="/contact" component={Contacts} />
                <Route path="/projects" component={Contacts} />
            </div>
        </BrowserRouter>
        )
            
    }
}

export default Routing
